.dashboard .carousel{overflow:auto;padding-left:30px;width:100%;position:relative}.dashboard .carousel::-webkit-scrollbar{width:20px}.dashboard .carousel::-webkit-scrollbar-track{box-shadow:inset 0 0 5px grey;border-radius:10px;margin:50px}.dashboard .carousel::-webkit-scrollbar-thumb{background:#4cabe2;border-radius:10px}.dashboard .carousel__container{transition:450ms -webkit-transform;transition:450ms transform;transition:450ms transform, 450ms -webkit-transform;font-size:0;white-space:nowrap;margin:70px 0px;padding-bottom:10px}.dashboard .carousel__container:hover .carousel-item{opacity:0.3}.dashboard .carousel__container:hover .carousel-item:hover{-webkit-transform:scale(1.5);transform:scale(1.5);opacity:1}

.dashboard .carousel-item{border-radius:20px;overflow:hidden;position:relative;display:inline-block;width:200px;height:250px;margin-right:10px;font-size:20px;cursor:pointer;transition:450ms all;-webkit-transform-origin:center left;transform-origin:center left}.dashboard .carousel-item:hover ~ .carousel-item{-webkit-transform:translate3d(100px, 0, 0);transform:translate3d(100px, 0, 0)}.dashboard .carousel-item__img{width:200px;height:250px;object-fit:cover}.dashboard .carousel-item__details{-webkit-align-items:flex-start;align-items:flex-start;background:linear-gradient(to top, rgba(0,0,0,0.9) 0%, rgba(0,0,0,0) 100%);bottom:0;display:-webkit-flex;display:flex;font-size:10px;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:flex-end;justify-content:flex-end;left:0;opacity:0;transition:450ms opacity;padding:10px;position:absolute;right:0;top:0}.dashboard .carousel-item__details--img{width:20px}.dashboard .carousel-item:hover .carousel-item__details{opacity:1}.dashboard .carousel-item__details--title{color:white;margin:5px 0px 0px 0px}.dashboard .carousel-item__details--subtitle{color:white;font-size:8px;margin:3px 0px}.dashboard .scale{transition:all 0.2s ease-in-out}.dashboard .scale:hover{-webkit-transform:scale(1.5);transform:scale(1.5)}.dashboard .tooltip{position:relative;display:inline-block;border-bottom:1px dotted black}.dashboard .tooltip .tooltiptext{visibility:hidden;width:110px;background-color:#fff;color:#000;text-align:center;border-radius:6px;padding:5px 0;position:absolute;z-index:1;top:-5px;left:110%}.dashboard .tooltip .tooltiptext::after{content:"";position:absolute;top:50%;right:100%;margin-top:-5px;border-width:5px;border-style:solid;border-color:transparent #fff transparent transparent}.dashboard .tooltip:hover .tooltiptext{visibility:visible}

